.rewiew_item {
    position: relative;
    width: 100%;
    max-width: 386px;
    height: 340px;

    background: rgba(255, 255, 255, 0.3);
    border-radius: 20px;
}

.rewiew_mark {
    margin: 30px 0 32px 40px;
}

.rewiew_item p {
    margin: 0 40px 50px 40px;

    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
}

.rewiew_body {
    position: absolute;
    display: flex;
    width: 100%;
    bottom: 0;
    background: #000;
    color: #ffffff;
    border-radius: 0px 0px 20px 20px;
}

.rewiew_man {
    width: 44px;
    height: 44px;
    margin: 20px 20px 0 40px;
}

.rewiew_user {
    margin: 22px 0;
}

.rewiew_user span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}

.rewiew_star img {
    margin-right: 4px;
    cursor: pointer;
}

.rewiew_star img:last-child {
    margin-right: 0;
}

@media (max-width: 1024px) {
    .rewiew_item {
        max-width: 482px;
    }
}

@media (max-width: 768px) {
    .rewiew_item {
        max-width: 354px;
        height: 374px;
    }
}

@media (max-width: 480px) {
    .rewiew_item {
        max-width: 256px;
        height: 340px;
    }
    .rewiew_mark {
        margin-left: 30px;
    }
    .rewiew_item p {
        margin: 0 30px 38px 30px;
        font-size: 16px;
        line-height: 24px;
    }
}

@media (max-width: 320px) {
    .rewiew_item {
        max-width: 240px;
    }
}