.app-store_btn {
    display: flex;
    justify-content: center;
    align-items: center;

    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.app-store_btn:hover {
    opacity: 0.6;
}

.app-store_btn img {
    margin-right: 12px;
}

.app-store_btn span {
    margin-top: 3px;

    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}


.google-play_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.google-play_btn:hover {
    opacity: 0.6;
}

.google-play_btn img {
    margin-right: 12px;
}

.google-play_btn span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}
.header-frame {
    display: none;
    position: absolute;
    width: 252px;
    height: 384px;
    right: 1px;
    top: 100px;
    padding: 40px 40px;
    z-index: 100;

    border: 1px solid;
    background: rgba(255, 255, 255);
    border-image-source: linear-gradient(180deg, #FFFFFF 0%, #FFFFFF 100%);
    border-radius: 10px;
}

.header-frame_link {
    margin-bottom: 20px;
}

.header-frame a {
    font-family: Raleway Regular;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;

    color: #000000;
    text-decoration: none;
}

