}

.navbar_menu {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
}

.hamburger {
    display: none;
    position: absolute;
    right: 10px;
    top: 34px;
    width: 36px;
    height: 36px;
    cursor: pointer;
}

