    width: 150px;
}

.header_btn .app-store_btn {
    margin-right: 20px;
}

.header .google-play_btn {
    margin-right: 64px;
}

.language {
    display: flex;
    align-items: center;

    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    text-decoration: none;
    color: #000000;
}

.language:hover {
    color: #000000;
    text-decoration: none;
}

@media (max-width: 1024px) {
    .header_btn .app-store_btn, .header .google-play_btn {
